import React from 'react'
import { message, Layout, Input, Select } from 'antd';
import SideNavBar from '../SideNavBar'
import '../../style.css'
import { Route, HashRouter as Router, Switch, Redirect } from 'react-router-dom';
import ChangeLog from '../ChangeLog';
import Dashboard from '../Dashboard';

const { Header, Footer } = Layout;
const { Option } = Select





export default class Index extends React.Component {
    state = {
        searchType: "title"
    }
    setSearchType = (val) => {
        this.setState({
            searchType: val
        })
    }

    searchTypeSelect =
        <Select defaultValue="title" className="select-before" onChange={(val) => this.setSearchType(val)}>
            <Option value="title">标题检索(模糊)</Option>
            <Option value="content">内容检索(模糊)</Option>
            <Option value="author">作者检索(模糊)</Option>
            <Option value="module">模块名检索(精确)</Option>
        </Select>

    render() {

        return (
            <Router>
                <Layout style={{ minHeight: '100vh' }}>
                    <SideNavBar />
                    <Layout className="site-layout">
                        <Header className="site-header-background">
                            {/* <NotificationOutlined style={{ marginRight: "1%" }} /> */}

                            <Input.Search
                                addonBefore={this.searchTypeSelect}
                                placeholder="请输入关键字"
                                allowClear
                                size="large"
                                onSearch={() => { message.info(<span>检索类型 '{this.state.searchType}' 不可用</span>) }}
                                enterButton
                                style={{ paddingTop: '12px', paddingRight: '0px' }}
                            />

                        </Header>
                        <Switch>

                            <Route exact path="/dashboard" component={Dashboard} />
                            <Route exact path="/common/changelog" component={ChangeLog} />
                            <Redirect from="/" to="/dashboard" />
                        </Switch>
                        <Footer style={{ textAlign: 'center' }}><small>合作伙伴 / <a href="https://www.wanvale.com">万谷听海</a> / <a href="https://www.wanvale.com">椰壳云</a><br />&copy;2021 听海代码库.</small></Footer>
                    </Layout>
                </Layout>
            </Router >
        );
    }
}